.home {
    padding: 0em 2.5em 4em 2.5em;
}
.home .research-header {
    margin-top: 1em;
    font-size: 2em;
    font-weight: 600;
    text-transform: capitalize;
}
.home .desc {
    margin: 1em 0 2em 0;
    line-height: 1.5;
}

.home .research-topics-container {
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}
.home .research-topics-container .research-topic {
    display: flex;
    flex-direction: column;
}
.home .research-topic .topic-title {
    margin: 1em 0;
    font-size: 1.25em;
    font-weight: bold;
}
.home .research-topic .topic-content-container {
    display: flex;
    flex-direction: row;
}
.home .research-topic:nth-child(even) .topic-content-container {
    flex-direction: row-reverse;
}
.topic-content-container .topic-image {
    width: 50%;
    margin-right: 1em;
    margin-left: 0em;
}
.home .research-topic:nth-child(even) .topic-image {
    width: 50%;
    margin-right: 0em;
    margin-left: 1em;
}
.topic-content-container .topic-content {
    line-height: 1.5;
    width: 50%;
}
.topic-content ul {
    margin-top: 1em;
    position: relative;
    left: 1.2em;
}
.topic-image img {
    display: block;
    width: 100%;
}


/* ================================================================= */
.news {
    background-color: rgb(50, 51, 53);
    color: #fefefe;
    box-shadow: inset 7px 0 9px -7px rgba(0, 0, 0, 0.4);
    padding: 2em 2.5em 2em 2.5em;
}
.news h2 {
    padding-top: 1em;
    font-size: 1.75em;
    font-weight: 600;
    text-transform: uppercase;
}
.year {
    margin: 2em 0 1em 0;
    font-size: 1.5em;
    font-weight: 500;
}
.article {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    display: flex;
    flex-direction: row;
    gap: 2em;
    padding-bottom: 1em;
    margin-bottom: 1em;
}
.datebox {
    background-color: #d52349;
    text-align: center;
    padding: 0.5em 1em 0.5em 1em;
    border-radius: 10px;
    width: 35px;
    height: 50px;
}
.datebox .month {
    font-size: 1em;
}
.datebox .date {
    font-size: 1.25em;
    font-weight: 500;
}
.article-content-container {
    padding-top: 0.5em;
}
.article-content-container .category {
    text-transform: capitalize;
    font-weight: 500;
    font-size: 1em;
    margin-bottom: 0.5em;
}
.article-content-container p {
    word-break: break-word;
}



@media only screen and (max-width: 768px) {
    .home {
        padding-left: 10px;
    }
    .home .research-topic .topic-content-container {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: 0 auto;
        gap: 1em;
    }
    .home .research-topic:nth-child(even) .topic-content-container {
        flex-direction: column;
    }
    .topic-content-container .topic-image {
        width: 100%;
    }
    .topic-content-container .topic-image img {
        display: block;
        width: 100%;
    }
    .home .research-topic:nth-child(even) .topic-content-container .topic-image {
        width: 100%;
    }


    .topic-content-container .topic-content {
        width: 100%;
    }
}