.sidebar {
    display: block;
    background-color: rgb(50, 51, 53);
    height: 100vh;
    color: #fefefe;
    overflow-y: auto;
    -webkit-transition: all 0.6s;
    -moz-transition: all 0.6s;
    -ms-transition: all 0.6s;
    -o-transition: all 0.6s;
    transition: all 0.6s;
    position: sticky;
    top: 0;
    width: 200px;
    transform: translateX(0);
}
.sidebar::-webkit-scrollbar {
    display: none;
}
.sidebar.hide {
    width: 0px;
    transition: all 0.6s;
    transform: translateX(-100%);
}
#bars {
    display: block;
    font-size: 1.25em;
    position: absolute;
    top: 1em;
    left: 15px;
    cursor: pointer;
}
#xmark {
    position: absolute;
    top: 10px;
    left: 88%;
    color: rgb(90, 91, 93);
    cursor: pointer;
    font-size: 1.25em;
}
.sidebar .sidebar-element {
    display: flex;
    flex-direction: column;
    gap: 0.2em;
    margin-bottom: 0.5em;
    padding-bottom: 2em;
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}
.sidebar .sidebar-element h2 {
    font-size: 1em;
    font-weight: 500;
    text-transform: capitalize;
    padding: 0 0 1em 1em;
    margin: 1.5em 0 1em 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.sidebar .sidebar-element a {
    text-decoration: none;
    font-size: 0.75em;
    text-transform: uppercase;
    display: flex;
    gap: 1em;
    align-content: center;
    margin: 0em 1em 0em 1em;
    padding: 0.5em;
    -webkit-transition: all 0.6s;
    -moz-transition: all 0.6s;
    -ms-transition: all 0.6s;
    -o-transition: all 0.6s;
    transition: all 0.6s;
}
.sidebar .sidebar-element a:hover {
    background-color: #d52349;
    border-radius: 10px;
    -webkit-transition: all 0.6s;
    -moz-transition: all 0.6s;
    -ms-transition: all 0.6s;
    -o-transition: all 0.6s;
    transition: all 0.6s;
}
.sidebar .sidebar-element p {
    font-size: 0.75em;
    padding: 0em 1em 1em 1em;
    word-break: break-word;
}
.sidebar .sidebar-element .contact {
    display: flex;
    gap: 1em;
    font-size: 0.75em;
    margin: 0em 1em 0em 1em;
    padding: 0.5em;
}
.sidebar .sidebar-element .contact i {
    font-size: 1.15em;
}
.sidebar .copyright {
    font-size: 0.75em;
    margin-top: 3em;
    text-align: center;
}

@media only screen and (max-width: 768px) {
    /* for smartphones */
    .sidebar {
        transform: translateX(-100%);
        transition: transform 0.3s;
        position: fixed;
        top: 0;
        left: 0;
        width: 200px;
        height: 100%;
        z-index: 999;
    }
    .sidebar.hide {
        width: 200px;
        display: block;
        transform: translateX(0);
    }
    #bars {
        display: block;
        font-size: 1.25em;
    }
    #xmark {
        font-size: 1.25em;
    }
}
