.publication {
    padding: 0em 2.5em 0em 2.5em;
}
.publication h1 {
    font-size: 2em;
    font-weight: 600;
    margin-top: 1em;
}
.publication .publication-type {
    margin-top: 2em;
    margin-bottom: 1em;
}
.publication .publication-type h2 {
    font-size: 1.5em;
    font-weight: 600;
    margin-bottom: 1em;
}
.publication .publication-type h3 {
    font-size: 17px;
    font-weight: 600;
    margin-top: 1em;
    margin-bottom: 1em;
}
.publication .publication-type .custom-list {
    list-style: none;
    padding-left: 23px;
    text-indent: -23px;
}

.publication .publication-type .custom-list li {
    margin-right: 1em;
}
.publication .publication-type .custom-list li::before {
    content: '[' counter(my-counter) '] ';
    counter-increment: my-counter;
}
.publication .publication-type ul li span {
    font-style: italic;
}


@media only screen and (max-width: 768px) {
    .publication {
        padding-left: 10px;
    }
}