.project {
    padding: 0em 2.5em 0em 2.5em;
}

.project h1 {
    font-size: 2em;
    font-weight: 600;
    margin-top: 1em;  
}
.project-type {
    margin-top: 1em;
    padding-bottom: 1em;
}
.project-type h2 {
    font-size: 1.5em;
    font-weight: 600;
    margin-bottom: 1em;
}
.project-type ul li {
    margin-bottom: 1em;
    position: relative;
    left: 1.5em;
    margin-right: 1em;
}
.project-type ul li span {
    font-style: italic;
}

@media only screen and (max-width: 768px) {
    .project {
        padding-left: 10px;
    }
}