.people {
    padding: 0em 2.5em 0em 2.5em;
}
.people h1 {
    font-size: 2em;
    font-weight: 600;
    margin-top: 1em;
}
.people .people-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1em;
    padding-bottom: 2em;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    justify-content: space-between;
}
.people .people-container h2 {
    font-size: 1.5em;
    font-weight: 600;
    margin-top: 1em;
    margin-bottom: 0.25em;
    flex-basis: 100%;
}
.people .people-container .person {
    width: 32%;
    display: flex;
    flex-direction: row;
    gap: 1em;
}
.people .people-container .person .image {
    width: calc(95px * 1.75);
    height: calc(113px * 2);
}
.people .people-container .person .image img {
    display: block;
    width: 100%;
    height: 100%;
}
.people .people-container .person .bio {
    padding-top: 1em;
    word-break: break-word;
    width: calc(100% - 95px * 1.75);
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.people .people-container .person .bio .name {
    font-size: 17px;
    font-weight: 600;
}
.people .people-container .person .bio .state {
    text-transform: capitalize;
}
.people .people-container .person .bio .map {
    color: #d52349;
}
.people .people-container-last {
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding-bottom: 2em;
}
.people .people-container-last h2 {
    font-size: 1.5em;
    font-weight: 600;
    margin-top: 1em;
    margin-bottom: 0.25em;
    flex-basis: 100%;
}
.people .people-container-last p {
    font-size: 1em;
}


@media only screen and (max-width: 768px) {
    .people {
        padding-left: 10px;
    }
    .people .people-container .person {
        width: 100%;
        flex-direction: column;
        margin-bottom: 30px;
        gap: 0;
    }
    .people .people-container .person .image {
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .people .people-container .person .image img {
        width: calc(95px * 1.75);
        height: calc(113px * 2);
    }
    .people .people-container .person .bio {
        gap: 0;
        width: 100%;
        text-align: center;
    }
    .people .people-container .person .bio .name {
        margin-bottom: 0.5em;
    }


}