.research {
    padding: 0em 2.5em 3em 2.5em;
}
.research .research-header {
    margin-top: 1em;
    font-size: 2em;
    font-weight: 600;
    text-transform: capitalize;
}
.research .desc {
    margin: 1em 0 2em 0;
    line-height: 1.5;
}

.research .research-topics-container {
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}
.research .research-topics-container .research-topic {
    display: flex;
    flex-direction: column;
}
.research .research-topic .topic-title {
    margin: 1em 0;
    font-size: 1.25em;
    font-weight: bold;
}
.research .research-topic .topic-content-container {
    display: flex;
    flex-direction: row;
}
.research .research-topic:nth-child(even) .topic-content-container {
    flex-direction: row-reverse;
}
.research .research-topic .topic-content-container .topic-image {
    width: 50%;
    margin-right: 1em;
    margin-left: 0em;
}
.research .research-topic:nth-child(even) .topic-image {
    width: 50%;
    margin-right: 0em;
    margin-left: 1em;
}
.research .research-topic .topic-content-container .topic-content {
    line-height: 1.5;
    width: 50%;
}
.research .research-topic .topic-content ul {
    margin-top: 1em;
    position: relative;
    left: 1.2em;
}
.research .research-topic .topic-image img {
    display: block;
    width: 100%;
}


@media only screen and (max-width: 768px) {
    .research {
        padding-left: 10px;
    }
    .research .research-topic .topic-content-container {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: 0 auto;
        gap: 1em;
    }
    .research .research-topic:nth-child(even) .topic-content-container {
        flex-direction: column;
    }
    .research .research-topic .topic-content-container .topic-image {
        width: 100%;
        margin: 0;
    }
    .topic-content-container .topic-image img {
        display: block;
        width: 100%;
    }
    .research .research-topic:nth-child(even) .topic-content-container .topic-image {
        width: 100%;
        margin: 0;
    }

    .research .research-topic .topic-content-container .topic-content {
        width: 100%;
    }
}