.about {
    padding: 0em 2.5em 0em 2.5em;
}

.about h2 {
    font-size: 2em;
    font-weight: 600;
    margin: 1em 0 0.5em 0;
}
.about h3 {
    font-size: 1.5em;
    font-weight: 500;
    margin-bottom: 0.5em;
}
.about .sponsor {
    display: flex;
    gap: 2em;
    margin-top: 2em;
    margin-bottom: 200px;
    flex-wrap: wrap;
}
.about .sponsor img {
    width: 200px;
}

@media only screen and (max-width: 768px) {
    .about {
        padding-left: 10px;
    }
    .about .sponsor img {
        flex-grow: 1;
    }
}